import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'CustomerMessageDetail',
    props: {
        id: {
            type: String,
            requried: true
        },
        type: {
            type: String,
            requried: true
        }
    },
    setup(props) {
        const showMsg = ref(false);
        const title = ref('');
        const subTitle = ref('');
        const msgContent = ref('');
        const sendTime = ref('');
        const getMessageInfo = () => {
            let url = '';
            if (props.type === 'sys') {
                url = '/hbSeller/fans/msg/system/detail';
            }
            else if (props.type === 'shop') {
                url = '/hbSeller/fans/msg/shop/detail';
            }
            http.post(url, { id: props.id }, false).then((res) => {
                if (res.code === '200') {
                    title.value = res.data.data.title;
                    subTitle.value = res.data.data.subTitle;
                    msgContent.value = res.data.data.msgContent;
                    sendTime.value = res.data.data.ctime
                        ? res.data.data.ctime
                        : res.data.data.sendTime;
                    showMsg.value = true;
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        onMounted(() => {
            getMessageInfo();
        });
        return {
            showMsg,
            title,
            subTitle,
            msgContent,
            sendTime
        };
    }
});
